import cTitle from 'components/title';
export default {
  data() {
    return {
      //提现总金额
      total_income: '',
      //可提现
      income: '',
      //插件数据源
      plugs: []
    }
  },

  methods: {
    getNetData(status) {
      var that = this;
      $http.get('finance.income.get-income-count', { status: 9 }, '正在获取列表...').then(function (response) {
        //console.log(response.data);
        if (response.result == 1) {
          var mydata = response.data;
          if(status.length ==0)
          {
            that.total_income = mydata.total.income;

          }else
          {
            that.plugs  = [];
            that.income = mydata.total.income;
            if(mydata.commission)
            {
              that.plugs.push(mydata.commission);

            }
            if(mydata.dividend)
            {
              that.plugs.push(mydata.dividend); 

            }
            
            
           

          }

        }
      }, function (response) {
        // error callback
      });

    },

  },
  activated () {
    this.getNetData('');//获取总的收入
    this.getNetData(0);//可提现的收入
  },
  components: { cTitle }
}


